.logo{display:inline-block;height:64px;padding:0 0 0 24px;font-size:20px;line-height:64px;vertical-align:top;cursor:pointer}.logo img{display:inline-block;vertical-align:middle}.menu :global(.anticon){margin-right:8px}.menu :global(.ant-dropdown-menu-item){min-width:160px}.trigger{height:64px;padding:22px 24px;font-size:20px;cursor:pointer;transition:all .3s,padding 0s}.trigger:hover{background:rgba(0,0,0,.025)}.right{float:right;height:100%;overflow:hidden}.right .action{display:inline-block;height:100%;padding:0 12px;cursor:pointer;transition:all .3s}.right .action>i{color:rgba(0,0,0,.65);vertical-align:middle}.right .action:hover{background:rgba(0,0,0,.025)}.right .action:global(.opened){background:rgba(0,0,0,.025)}.right .search{padding:0 12px}.right .search:hover{background:transparent}.right .account .avatar{margin:20px 8px 20px 0;color:#1890ff;vertical-align:top;background:hsla(0,0%,100%,.85)}.dark{height:64px}.dark .action{color:hsla(0,0%,100%,.85)}.dark .action>i{color:hsla(0,0%,100%,.85)}.dark .action:global(.opened),.dark .action:hover{background:#1890ff}.dark .action :global(.ant-badge){color:hsla(0,0%,100%,.85)}@media only screen and (max-width:768px){:global(.ant-divider-vertical){vertical-align:unset}.name{display:none}i.trigger{padding:22px 12px}.logo{position:relative;padding-right:12px;padding-left:12px}.right{position:absolute;top:0;right:12px;background:#fff}.right .account .avatar{margin-right:0}}